@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600&display=swap);
:root {
  --color-primary: #36c5f0;
  --color-bg-primary: #283149;
  --main-accent: #e9eefd;
  --text-primary: #f7f7f7;
  --text-title: #f7f7f7;
  --text-highlight: #7ddaf5;
  --shadow: rgba(17, 17, 26, 0.1) 2px 2px 16px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.profile{display:grid;grid-template-columns:4.5rem auto;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;align-items:center;padding:2rem 0.3rem 1rem;border-radius:7px;margin:2.5rem 0}.profile .profile__img-container .profile__img{width:100%;height:auto;border-radius:50%}.profile .profile__content{display:flex;justify-content:space-between;align-items:center}

li{list-style:none}li a{display:block;padding:0.7rem 0;text-decoration:none;color:inherit}li a span:first-child{display:inline-block;margin-right:0.7rem;font-size:1.4rem;color:var(--color-primary)}li .active{background-color:var(--color-primary);border:1px solid var(--main-accent);padding:0.8rem;border-radius:7px;color:red}li .active span{color:var(--main-accent)}

.menu{margin-bottom:2rem}.menu a{color:var(--text-primary)}

.card{text-align:center;padding:1.8rem 1.5rem;border-radius:15px;margin-top:2rem;border:1px solid var(--main-accent)}.card .card__icon{margin-bottom:0.8rem}.card .card__icon span{display:inline-block;color:var(--color-primary);font-size:8rem}.card .card__icon+div{margin-bottom:1.2rem}.card .card__icon+div p{font-size:0.8rem;color:var(--text-title)}.card .card__icon+div .button{margin-top:2rem;padding:0.8rem 1rem;border:none;border-radius:10px}.card .card__icon+div .button--main{background-color:var(--color-primary);color:#fff}.card .card__icon+div .button--block{display:block;width:100%}

.sidebar{width:22.5rem;height:100%;padding:1rem;position:fixed;z-index:30;top:0%;left:-22.5rem;transition:left 0.3s;color:var(--text-primary)}@media screen and (max-width: 70.25rem){.sidebar{left:-22.5rem;z-index:5}}@media screen and (max-width: 28.375rem){.sidebar{width:80%;min-width:275px}}.sidebar .sidebar__container{height:100%;background-color:var(--color-bg-primary);border-radius:20px;box-shadow:var(--shadow);padding:1rem 2rem;overflow-y:auto}.sidebar .sidebar__container::-webkit-scrollbar{width:5px}.sidebar .sidebar__container::-webkit-scrollbar-track{background-color:#f1f1f1}.sidebar .sidebar__container::-webkit-scrollbar-thumb{background-color:#888;border-radius:10px}.sidebar .sidebar__container::-webkit-scrollbar-thumb:hover{background-color:var(--text-title)}.sidebar .sidebar__container h2{color:var(--text)}.sidebar .sidebar__container h2 span{color:var(--color-primary);font-size:2rem;display:inline-block;margin-right:0.5rem}

.button__container .button{padding:0.85rem 2rem;border:none;border-radius:10px;display:flex;align-items:center;font-size:1rem}.button__container .button--main{border:1px solid var(--main-accent);background-color:var(--color-primary);color:#fff}.button__container .button--main span{font-size:1.2rem;display:inline-block;margin-right:0.7rem}

.content{position:relative;width:100vw;padding:1rem 2rem 0;min-width:280px}.content header{display:flex;justify-content:space-between;padding-top:0.5rem}@media screen and (max-width: 31.25rem){.content header{align-items:center;flex-direction:column}}.content header .content__title-wrapper{display:flex}@media screen and (max-width: 31.25rem){.content header .content__title-wrapper{align-self:flex-start;align-items:center;justify-content:flex-start}}.content header .content__title-wrapper label{display:inline-block;color:var(--main-accent);margin-right:1rem;font-size:1.75rem}.content header .content__title h1{color:var(--text-primary);font-weight:600}@media screen and (max-width: 31.25rem){.content header .content__title h1{font-size:2rem;margin-left:1.5rem}}.content header .content__title p{color:var(--text-highlight);font-size:1.2rem}@media screen and (max-width: 31.25rem){.content header .content__title p{display:none}}.content header .content__title p span{display:inline-block;margin-left:0.5rem;color:var(--main-accent);font-size:1.2rem}@media screen and (max-width: 31.25rem){.content .content__button{display:none}}

.statistic{border:1px solid var(--main-accent);padding:1.5rem;border-radius:10px;display:flex;justify-content:center}@media screen and (max-width: 36.25rem){.statistic{justify-content:flex-start}}.statistic .statistic__icon{width:2.5rem;height:2.5rem;border-radius:50%;display:grid;place-items:center;margin-right:0.8rem;border:1px solid var(--main-accent)}@media screen and (max-width: 36.25rem){.statistic .statistic__icon{margin:0 1.5rem;align-self:center}}.statistic .first-child{color:#6883db}.statistic .second-child{color:red}.statistic .third-child{color:#2ec3a3}.statistic .fourth-child{color:var(--color-primary)}.statistic .statistic__info h4{font-weight:400;color:var(--text-title);font-size:0.95rem;margin-bottom:0.5rem}.statistic .statistic__info h1{display:flex;color:var(--text-primary);font-weight:600}.statistic .statistic__info h1 .text-danger{color:red}.statistic .statistic__info h1 .text-success{color:#2ec3a3}.statistic .statistic__info h1 .text-primary{color:var(--text-primary)}.statistic .statistic__info h1 small{color:var(--text-primary);font-weight:700;font-size:0.8rem}.statistic:first-child .analytic-icon{background-color:#dce5ff;color:#688}

.graph__content{border:1px solid var(--main-accent);border-radius:12px;padding:1rem;background-color:var(--color-bg-primary)}.graph__content .graph__header{display:flex;justify-content:space-between;align-items:center}.graph__content .graph__header .graph__icon-wrapper{display:flex;align-items:center}.graph__content .graph__header .graph__icon{display:grid;place-items:center;height:2.5rem;width:2.5rem;border-radius:7px;background-color:#eee;font-size:1.2rem;margin-right:0.8rem}@media screen and (max-width: 18.75rem){.graph__content .graph__header .graph__icon{display:none}}.graph__content .graph__header .graph__icon .views{color:#6883db}.graph__content .graph__header .graph__icon .watch-time{color:#2ec3a3}.graph__content .graph__header select{background-color:#fbfefd;border-radius:10px;height:2.1875rem;padding:0.5rem 1rem;border:none;outline:none}.graph__content .graph__canvas #revenue-graph{display:block;width:100%;height:51px;transition:width 0.3s}

.profit__container{display:grid;grid-template-columns:30% auto;grid-gap:3rem}@media screen and (max-width: 53.75rem){.profit__container{grid-template-columns:100%;order:2}}@media screen and (max-width: 53.75rem){.profit__container .profit{order:2}}.profit__container .profit .profit__card-content{border:1px solid var(--main-accent);text-align:center;border-radius:15px;padding:2rem 0;display:grid;place-items:center}.profit__container .profit .profit__card-content img{width:11.25rem;margin:1rem 0;border-radius:50%}.profit__container .profit .profit__card-content .profit__info{margin-bottom:1rem}.profit__container .profit .profit__card-content .profit__info h1{font-weight:600;margin-top:0.2rem;font-size:1.5rem;color:var(--text-title)}.profit__container .profit .profit__card-content .profit__info h1,.profit__container .profit .profit__card-content .profit__info h3{color:var(--text-primary)}.profit__container .profit .profit__card-content .profit__info h1 small{font-weight:inherit;font-size:1rem}.profit__container .profit .profit__card-content .profit__sum{border:1px solid var(--main-accent);padding:1rem;margin-top:1rem;width:calc(100% - 5rem);border-radius:10px}.profit__container .profit .profit__card-content .profit__sum h2{font-size:1.4rem;color:var(--text-primary)}.profit__container .profit .profit__card-content .profit__sum h4{font-size:1rem;color:var(--text-title);margin-bottom:0.5rem}.profit__container .profit .profit__card-content .profit__sum h2,.profit__container .profit .profit__card-content .profit__sum h4{font-weight:600}

main{padding:2.5rem 0}main .section-header{font-size:1.4rem;color:var(--text-primary);font-weight:600;margin-bottom:1rem}main .statistics{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:0 2.5rem;margin-bottom:3rem;position:relative;padding:0 0 4rem 0}main .statistics::after{position:absolute;z-index:1;content:"";bottom:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);left:50%;height:1px;width:90%;background-color:var(--main-accent)}@media screen and (max-width: 53.75rem){main .statistics{grid-template-columns:repeat(2, 1fr);grid-gap:2.5rem}}@media screen and (max-width: 42rem){main .statistics{grid-template-columns:100%}}

.container{height:100vh;background-color:var(--color-bg-primary);overflow-x:hidden;font-size:1.2rem}.container .overlay{position:fixed;z-index:2;left:0;top:0;width:100%;height:100%;background-color:rgba(26,26,26,0.8);display:none}.container .overlay label{display:block;height:100%;width:100%}.container #menu-toggle{display:none}.container #menu-toggle:checked ~ .sidebar{left:0}.container #menu-toggle:checked ~ .overlay{display:block;text-align:right}

